@import "../../config";

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba($color: #000000, $alpha: 0.7);
}

.part-wrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 85vh;
  width: 90vw;
  background: url("/media/img/paper-menu-background.jpg");
  background-size: contain;
  color: $primary-color;
  padding: 10px;
  border-radius: 10px;
  .part-close-button {
    position: absolute;
    right: 10px;
    font-size: 1.5rem;
    font-weight: 600;
    cursor: pointer;
  }
  .part-information{
    .part-name{
      width: 90%;
      margin-bottom: 10px;
    }
    p{
      font-size: 1.2rem;
      margin: 10px 0;
    }
  }
}